<template>
  <div>
    <div class="module filter">
      <div class="l">
        <el-input placeholder="请输入内容" v-model="form.keyword">
          <template slot="prepend">搜索主体</template>
        </el-input>
      </div>

      <div class="l">
        <el-button @click="getList()">搜索</el-button>
      </div>
    </div>

    <div class="module">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="id" label="系统ID" width="100" align="center"> </el-table-column>

        <el-table-column prop="name" label="网关设备名称" >
        </el-table-column>

        <el-table-column prop="accesskey" label="TCP注册包访问密钥" width="280">
        </el-table-column>

        <el-table-column prop="hex" label="转译16进制密钥" width="280">
        </el-table-column>

        <el-table-column prop="create_time" label="创建时间" width="250">
        </el-table-column>

        <el-table-column label="更多" fixed="right" width="125" align="center">
          <template slot-scope="scope">
            <el-dropdown>
              <el-button type="primary">
                管理<i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <router-link :to="{path:'/iot/equipment/add', query:{rowId: updateTable.id}}">
                  <el-dropdown-item @click.native="goEquip(scope.row)">添加从机</el-dropdown-item>
                </router-link>
                <router-link :to="{path:'/iot/equipment', query:{rowId: updateTable.id}}">
                  <el-dropdown-item @click.native="detail(scope.row)">管理网关下从机</el-dropdown-item>
                </router-link>
                <el-dropdown-item @click.native="updatedia(scope.row)">修改网关信息</el-dropdown-item>
                <el-dropdown-item @click.native="dele()">删除</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>

      </el-table>

      <el-dialog title="修改设备" :visible.sync="dialogVisible" width="30%" :show-close='false'
        :close-on-click-modal='false'>
        <div class="module">
          <div class="mtitle">设备名称</div>
          <el-input v-model="updateTable.name" placeholder="设备名称"></el-input>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click.native="updateList(1)">确 定</el-button>
        </span>
      </el-dialog>
    </div>

    <div class="module">
      <Pagination @pagination="getList()" :page.sync="form.querypage" :pageCount.sync="totalPage" :total.sync="total"
        v-if="total > 0" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { Query, AddorEdit } from "@/api/gateway";
import Pagination from "@/components/Pagination.vue";
export default {
  name: "Home",
  data() {
    return {
      form: {
        keyword: "",
        querypage: 1
      },
      tableData: [], // 列表
      total: 0, // 总条目
      totalPage: 0, // 总页数

      rowId: "",

      dialogVisible: false,
      updateTable: []
    };
  },
  components: {
    Pagination
  },
  methods: {
    getList() {
      if (this.rowId != "") {
        this.form.principal_id = this.rowId;
      }
      this.$http(Query(this.form), res => {
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success"
          });
          this.total = res.data.total;
          this.form.querypage = res.data.currentPage;
          this.totalPage = res.data.totalPages;
          this.tableData = res.data.result;
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000
          });
        }
      });
    },
    updatedia(item) {
      this.dialogVisible = true;
      this.updateTable = item;
    },
    dele() {
      this.$confirm(
        "",
        "这是一段内容确定删除吗？",
        () => {
          //删除接口
          // this.$http(
          //   Managermainspecies({
          //     id: id,
          //     set: 1
          //   }),
          //   res => {
          //     if (res.code == 200) {
          //       this.$message({
          //         message: res.msg,
          //         type: "success"
          //       });
          //       this.getList();
          //     } else {
          //       this.$message({
          //         message: res.msg,
          //         type: "error",
          //         duration: 5 * 1000
          //       });
          //     }
          //   }
          // );
        },
        () => {
          this.$message("已取消");
        }
      );
    },
    detail(item) {
      this.updateTable = item;
    },
    updateList(type) {
      console.log(type);
      if (type == 1) {
        this.$http(
          AddorEdit({
            id: this.updateTable.id,
            principal_id: this.updateTable.principal_id,
            name: this.updateTable.name
          }),
          res => {
            if (res.code == 200) {
              this.$message({
                message: res.msg,
                type: "success"
              });

              this.dialogVisible = false;
            } else {
              this.$message({
                message: res.msg,
                type: "error",
                duration: 5 * 1000
              });
            }
          }
        );
      }
    },
    goEquip(item) {
      this.updateTable = item;
      console.log(this.updateTable);
    }
  },
  created() {
    this.rowId = this.$route.query.rowId || "";
    this.getList();
  }
};
</script>
<style lang="scss" scoped>
</style>